    line-height: 15px;
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    background-color: white;
    color: #a0a0a0;
  }
}
@media screen and (max-width: 768px) {
  .movies-card-list {
    padding: 40px 10px 30px 10px;
  }
  .movies-card-list__else {
    margin: 0 auto;
    width: 240px;
  }
}
